.btnButton {
  margin: auto;
  display: block;
  margin-top: 16px;
  background: #38369a;
  padding: 4px;
  border-radius: 4px;
  color: #fff;
  width: 160px;
}

.tick {
  display: inline-block;
  transform: rotate( 45deg);
  height: 13px;
  width: 7px;
  border-bottom: 3px solid rgb(56, 54, 154);
  border-right: 3px solid rgb(56, 54, 154);
  margin: 0px 9px;
}

